/* Cart badge */
.cart-link {
  position: relative;
  display: inline-block;
}
.cart-badge {
  position: absolute;
  top: -10px;
  right: -18px;
  min-width: 22px;
  height: 22px;
  background: #ffd600;
  color: #1976d2;
  font-weight: bold;
  font-size: 0.95rem;
  border-radius: 50%;
  text-align: center;
  line-height: 22px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.08);
  border: 2px solid #fff;
  z-index: 2;
}
nav {
  background: #1976d2;
  border-radius: 0 0 20px 20px;
  padding: 1.5rem 0 1.5rem 0;
  margin-bottom: 2rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}
/* SoftMarkt brand top left */
.navbar-brand {
  color: #fff;
  font-size: 2rem;
  font-weight: bold;
  margin-left: 2.5rem;
  letter-spacing: 1px;
  font-family: "Montserrat", "Segoe UI", Arial, sans-serif;
  user-select: none;
}
ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  list-style: none;
  margin: 0;
  padding: 0 3rem 0 0;
  gap: 2.5rem;
  background: none;
  position: static;
  top: unset;
  right: unset;
  transform: none;
}
ul li {
  margin: 0;
}
ul li a {
  color: #fff;
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none;
  transition: color 0.2s;
}
ul li.active a,
ul li a:hover {
  color: #ffd600;
}
@media (max-width: 900px) {
  nav {
    padding: 1rem 0 1rem 0;
  }
  .navbar-brand {
    font-size: 1.2rem;
    margin-left: 1rem;
  }
  ul {
    gap: 1.2rem;
    padding-right: 1rem;
  }
  ul li a {
    font-size: 1.1rem;
  }
}
